import axios from "axios";
import env from "./env.js";
const config = {
    baseURL: env.basepath,
    headers: {
        'Content-Type': 'application/json',
    },
};
const api = axios.create(config);
export default {
    listBoards: () => api.get('/api/internal/v1/list/boards'),
    createThread: (data) => fetch('/api/internal/v1/create/thread', {
        method: 'POST',
        headers: { accept: 'application/json' },
        body: data
    }),
    createPost: (data) => fetch('/api/internal/v1/create/post', {
        method: 'POST',
        headers: { accept: 'application/json' },
        body: data
    }),
    generateToken: () => fetch('/api/v1/internal/create/token'),
    setToken: (token) => api.post('/api/v1/internal/set/token', { token }),
};
