import { writable } from "svelte/store";
import Thread from "../Models/Thread";
class Toolbar {
    constructor() {
        this.open = false;
        this.threads = [];
    }
}
export const toolbar = writable(new Toolbar());
export const toggleToolbar = () => toolbar.update(t => { t.open = !t.open; return t; });
export const consumePrepender = (prefetched) => toolbar.update(r => {
    prefetched.forEach(thread => {
        const newThread = new Thread();
        newThread.hydrate(Thread, thread);
        r.threads.push(newThread);
    });
    r.threads = prefetched;
    return r;
});
